
import { formatDate } from "@/common/js/util"
import { callPuExctQryToOICS } from "@/api/api"
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import OPulldownPullupList from "@/components/common/OPulldownPullupList";
import OFooter from "@/components/common/OFooter";
export default {
components: {
OPulldownPullupList,
OFooter,
ButtonTab,
ButtonTabItem,
Divider,
ViewBox,
Badge,
AlertModule
},
data() {
return {
selectValue: 0,//默认选择今日任务
object: {},
pageType: {
typeValue: "1",
typeName: "ROTR",
titleName: "日常工作跟踪",
ruleName: "GetTodayTaskList",
linkName: "routines_track/track_details"
},
taskType: "GetTodayTaskList",
isShowPulldownPullupList: false
}
},
methods: {
consoleIndex() {
console.log('click demo01', this.demo01)
},//切换显示今日、本周、本月任务
showTaskList(key) {
this.$store.commit('updateLoadingStatus', { isLoading: true })
if (key == 'TodayTask') { this.pageType.ruleName = "GetTodayTaskList"; this.taskType = "GetTodayTaskList" }
else if (key == 'WeekTask') { this.pageType.ruleName = "GetWeekTaskList"; this.taskType = "GetWeekTaskList" }
else if (key == 'MonthTask') { this.pageType.ruleName = "GetMonthTaskList"; this.taskType = "GetMonthTaskList" }

},//获取待办任务数
getDailyWorkCount() {
let params = {
params: {
RuleName: "GetDailyWorkCount",
Parm: { TimePoint: formatDate(new Date(), 'yyyyMMdd') }
}
};
let callBackSuccess = data => {
if (data.ReturnCd == '00') {
this.object = data.RspMsg.result[0];
this.isShowPulldownPullupList = true;
}
else {
this.showModule("获取数据失败", data.RspMsg.msg);
}
};
callPuExctQryToOICS(params, callBackSuccess);
},//获取任务名称
getTaskName(key) {
let taskName = "";
if (key == 'TodayTask') taskName = "今日任务"
else if (key == 'WeekTask') taskName = "本周任务"
else if (key == 'MonthTask') taskName = "本月任务"
return taskName;
},//弹出信息提示窗
showModule(title, content) {
AlertModule.show({
title: title,
content: content,
onShow() {

},
onHide() {

}
})
}
},
beforeRouteEnter(to, from, next) {
next(vm => {
vm.$store.commit('updateLoadingStatus', { isLoading: true })
vm.object = {};
vm.getDailyWorkCount();
vm.$refs.pulldownPullupList.pageIndex = 0;
vm.$refs.pulldownPullupList.showloading = false;
vm.$refs.pulldownPullupList.list = [];
vm.$refs.pulldownPullupList.getList(() => { });
vm.$refs.pulldownPullupList.$refs.scroller.reset({ top: 0 });
})
}
}



